.about__me-img{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 50%;
    overflow: hidden;
}

.about__cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}
.about__card{
    background: var(--color-bg-varient);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}
.about__card > h6{
    margin-top: 10px;
    font-size: 0.7rem;
    color: var(--color-light);
}
.about__card > h5{
    font-weight: 400;
    font-size: 1.1rem;
}
.about__icon{
    color: var(--color-primary);
    font-size: 1.3rem;
    margin-bottom: 1rem;
}

.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-varient);
    cursor: default;
}

.about__content p{
    margin: 2rem 0 0.8rem;
    color: var(--color-light);
}

/* ========= MEDIA QUERIES (MEDIUM DEVICES) ======== */

@media screen and (max-width: 1024px) {
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content p{
        margin: 1rem 0 1.5rem;
    }
}

/* ========= MEDIA QUERIES (SMALL DEVICES) ======== */

@media screen and (max-width: 600px) {
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about__cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
    .about__content{
        text-align: center;
    }
    .about__content p{
        margin: 1.5rem 0;
    }
}